@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,600;1,400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, .2);
}

body {
  margin: 0;
  font-family: 'Poppins', 'Segoe UI', Helvetica Neue, Helvetica, Lucida Grande, Arial;
}

.app-window {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.MainContent {
  flex: 1;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: row;
  overflow: hidden;
  max-width: 100%;
}

/* Sidebar */
.sidebar {
  width: 35%;
  height: 100%;
  max-width: 415px;
  display: flex;
  background-color: #fff;
  flex-direction: column;
}

.sidebar--topo {
  width: 100%;
  height: 50px;
  background-color: #1EBE71;
}

.sidebar--content {
  display: flex;
  flex-direction: row;
  height: 90%;
  max-height: 90%;
}

.sidebar--actions {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80px;
  padding-top: 30px;
}

.sidebar--chatlist {
  margin-top: -50px;
  background-color: #fff;
  flex: 1;
  border-radius: 20px 20px 0 0;
  max-height: 100%;
  overflow-y: auto;
}

.sidebar--chatlist-disturb {
  height: 60px;
  width: 100%;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.sidebar--chatlist-newChat {
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #eee;
}

.sidebar--chatlist-newChat h1 {
  font-size: 16px;
  margin-bottom: 5px;
  font-weight: 600;
}

.sidebar--chatlist-newChat p {
  font-size: 12px;
  color: #1EBE71;
  margin-bottom: 15px;
}

.sidebar--chatlist--btn-more {
  background-color: #1EBE71;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, .2);
}

.sidebar--actions-label {
  color: '#A5A5A5';
  font-size: 12px;
}

.sidebar--actions--btn {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease .3s;
  margin-bottom: 20px;
}

.sidebar--actions--btn:hover {
  background-color: #F5F5F5;
}

/* chatList */
.chatlist {
  flex: 1;
  background-color: #FFF;
  overflow-y: auto;
}

.chat-notfound {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease .3s;
  margin-bottom: 20px;
}

.contentarea {
  flex: 1;
  height: 100%;
}

.listening-mic {
  transition: 1s all ease;
  animation: listening-mic 1.5s infinite;
}

.listening-div {
  transition: 1s all ease;
  animation: listening-div 1.5s infinite;
}

@keyframes listening-mic {
  50% {
    color: #1EBE71;
  }
}

@keyframes listening-div {
  50% {
    color: white;
  }
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .sidebar {
    width: 40%;
  }
}

@media (max-width: 768px) {
  .sidebar {
    width: 50%;
  }

  .sidebar--topo {
    height: 60px;
  }

  .sidebar--chatlist-newChat h1 {
    font-size: 14px;
  }

  .sidebar--chatlist-newChat p {
    font-size: 10px;
  }

  .sidebar--actions {
    width: 70px;
    padding-top: 20px;
  }

  .sidebar--actions--btn {
    height: 60px;
  }
}

@media (max-width: 480px) {
  .sidebar {
    width: 100%;
    height: auto;
  }

  .sidebar--topo {
    height: 50px;
  }

  .sidebar--content {
    flex-direction: column;
  }

  .sidebar--chatlist {
    margin-top: 0;
    height: 100%;
  }

  .sidebar--chatlist-newChat {
    height: auto;
    padding: 10px;
  }

  .sidebar--chatlist-newChat h1 {
    font-size: 12px;
  }

  .sidebar--chatlist-newChat p {
    font-size: 8px;
  }

  .sidebar--actions {
    width: 60px;
    padding-top: 10px;
  }

  .sidebar--actions--btn {
    height: 50px;
  }

  .contentarea {
    padding: 10px;
  }
}
