@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,600;1,400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, .2);
}

body {
  margin: 0;
  font-family: 'Poppins', 'Segoe UI', Helvetica Neue, Helvetica, Lucida Grande, Arial;
}

.app-window {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.MainContent {
  flex: 1 1;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: row;
  overflow: hidden;
  max-width: 100%;
}

/* Sidebar */
.sidebar {
  width: 35%;
  height: 100%;
  max-width: 415px;
  display: flex;
  background-color: #fff;
  flex-direction: column;
}

.sidebar--topo {
  width: 100%;
  height: 50px;
  background-color: #1EBE71;
}

.sidebar--content {
  display: flex;
  flex-direction: row;
  height: 90%;
  max-height: 90%;
}

.sidebar--actions {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80px;
  padding-top: 30px;
}

.sidebar--chatlist {
  margin-top: -50px;
  background-color: #fff;
  flex: 1 1;
  border-radius: 20px 20px 0 0;
  max-height: 100%;
  overflow-y: auto;
}

.sidebar--chatlist-disturb {
  height: 60px;
  width: 100%;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.sidebar--chatlist-newChat {
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #eee;
}

.sidebar--chatlist-newChat h1 {
  font-size: 16px;
  margin-bottom: 5px;
  font-weight: 600;
}

.sidebar--chatlist-newChat p {
  font-size: 12px;
  color: #1EBE71;
  margin-bottom: 15px;
}

.sidebar--chatlist--btn-more {
  background-color: #1EBE71;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, .2);
}

.sidebar--actions-label {
  color: '#A5A5A5';
  font-size: 12px;
}

.sidebar--actions--btn {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease .3s;
  margin-bottom: 20px;
}

.sidebar--actions--btn:hover {
  background-color: #F5F5F5;
}

/* chatList */
.chatlist {
  flex: 1 1;
  background-color: #FFF;
  overflow-y: auto;
}

.chat-notfound {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease .3s;
  margin-bottom: 20px;
}

.contentarea {
  flex: 1 1;
  height: 100%;
}

.listening-mic {
  transition: 1s all ease;
  animation: listening-mic 1.5s infinite;
}

.listening-div {
  transition: 1s all ease;
  animation: listening-div 1.5s infinite;
}

@keyframes listening-mic {
  50% {
    color: #1EBE71;
  }
}

@keyframes listening-div {
  50% {
    color: white;
  }
}

/* Responsive Styles */
@media (max-width: 1024px) {
  .sidebar {
    width: 40%;
  }
}

@media (max-width: 768px) {
  .sidebar {
    width: 50%;
  }

  .sidebar--topo {
    height: 60px;
  }

  .sidebar--chatlist-newChat h1 {
    font-size: 14px;
  }

  .sidebar--chatlist-newChat p {
    font-size: 10px;
  }

  .sidebar--actions {
    width: 70px;
    padding-top: 20px;
  }

  .sidebar--actions--btn {
    height: 60px;
  }
}

@media (max-width: 480px) {
  .sidebar {
    width: 100%;
    height: auto;
  }

  .sidebar--topo {
    height: 50px;
  }

  .sidebar--content {
    flex-direction: column;
  }

  .sidebar--chatlist {
    margin-top: 0;
    height: 100%;
  }

  .sidebar--chatlist-newChat {
    height: auto;
    padding: 10px;
  }

  .sidebar--chatlist-newChat h1 {
    font-size: 12px;
  }

  .sidebar--chatlist-newChat p {
    font-size: 8px;
  }

  .sidebar--actions {
    width: 60px;
    padding-top: 10px;
  }

  .sidebar--actions--btn {
    height: 50px;
  }

  .contentarea {
    padding: 10px;
  }
}

/* src/components/ChatListItem.css */
.chatListItem {
  display: flex;
  align-items: center;
  padding: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.chatListItem.active {
  background-color: #ebebeb;
}

.chatListItem:hover {
  background-color: #f5f5f5;
}

.chatListItem--avatar {
  width: 50px;
  height: 50px;
  background-color: black; /* Solid black color */
  border-radius: 50%; /* Circular shape */
  margin-right: 15px; /* Space between avatar and text */
}

.chatListItem--details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1;
}

.chatListItem--name {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px; /* Space between name and last message */
}

.chatListItem--lastMsg {
  color: #999;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.chatListItem--date {
  font-size: 12px;
  color: #999;
  align-self: flex-end; /* Align the time to the right */
}

/* src/components/ChatIntro.css */
.chatIntro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-align: center;
  background-color: #f5f5f5; /* Optional background color */
}

.chatIntro-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.chatIntro h1 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.chatIntro h2 {
  font-size: 18px;
  color: #555;
  margin-bottom: 20px;
}

.chatIntro p {
  font-size: 16px;
  color: #777;
}

/* ChatWindow.css */
.chatWindow {
  display: flex;
  flex-direction: column;
  height: 100%;
 background-image: url('https://www.shutterstock.com/image-vector/social-media-sketch-vector-seamless-600nw-1660950727.jpg')
}

.chatWindow--header {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #28a12e;
  border-bottom: 1px solid #ddd;
}

.chatWindow--avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.chatWindow--title {
  font-size: 16px;
  font-weight: bold;
}

.chatWindow--messages {
  flex: 1 1;
  padding: 10px;
  overflow-y: auto;
}

.message {
  margin-bottom: 10px;
  display: flex;
}

.message.incoming {
  justify-content: flex-start;
}

.message.outgoing {
  justify-content: flex-end;
}

.message--text {
  background-color: #fff;
  padding: 10px;
  border-radius: 10px;
  max-width: 70%;
}

.chatWindow--input {
  display: flex;
  padding: 10px;
  border-top: 1px solid #ddd;
  background-color: #fff;
}

.chatWindow--input input {
  flex: 1 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 20px;
}

.chatWindow--input button {
  margin-left: 10px;
  padding: 10px 20px;
  border: none;
  background-color: #1DAB67;
  color: #fff;
  border-radius: 20px;
  cursor: pointer;
}

/* NewChat.css */
.newChat {
  position: fixed;
  width: 30%;
  height: 100%;
  right: 0;
  top: 0;
  background-color: white;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.2);
  z-index: 1000; /* Ensure it's above other elements */
  overflow-y: auto; /* Allow scrolling if content overflows */
  transition: top 0.3s ease-in-out; /* Smooth transition */
}

.sidebar--topo {
  height: 60px;
}

.newChat--list {
  padding: 10px;
}

.newChat--item {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc; /* Add a line between items */
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.newChat--item:hover {
  background-color: #f5f5f5;
}

.newChat--itemAvatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 15px;
  background-color: black;
}

.newChat--itemName {
  font-size: 16px;
  font-weight: bold;
}

.newChat--closebutton {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1100;
}

/* NewChat.css */
.newChat--attachment {
  position: absolute;
  bottom: 20px; /* Adjust this value to move the icon down */
  left: 20px; /* Adjust this value for horizontal positioning */
  cursor: pointer;
  z-index: 1100;
  font-size: 30px; /* Adjust size if needed */
  color: #1DAB67; /* Color of the icon */
}



.sidebar--content {
  padding: 20px 0;
}

.sidebar--chatlist-top {
  display: flex;
